<template>
  <div>
    <div class="login-container">
      <div style="padding-top: 20px" @click="Tologin()">
        <i class="el-icon-caret-left"></i>
        <i class="el-icon-caret-left"></i>
        <i class="el-icon-caret-left"></i>
      </div>
      <el-card shadow class="login-card">
        <img
          class="logo"
          src="../assets/AssociationLogin/back.png"
          alt="Logo"
        />
        <el-select
          v-model="college"
          placeholder=""
          class="login-select"
          @change="SelectCollege()"
          clearable
        >
          <el-option
            label=""
            selected
            value=""
          ></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
        <el-select
          v-model="major"
          placeholder=""
          class="login-select"
          clearable
          v-if="!showTextSelect"
          @change="changeMajorIt()"
        >
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option
            label=""
            value=""
            clearable
          ></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
        <el-select
          v-model="year"
          placeholder=""
          clearable
          class="login-select"
        >
          <el-option label="2015" value="2015"></el-option>
          <el-option label="2016" value="2016"></el-option>
          <el-option label="2017" value="2017"></el-option>
          <el-option label="2018" value="2018"></el-option>
          <el-option label="2019" value="2019"></el-option>
          <el-option label="2020" value="2020"></el-option>
          <el-option label="2021" value="2021"></el-option>
          <el-option label="2022" value="2022"></el-option>
          <el-option label="2023" value="2023"></el-option>
          <el-option disabled label="2024" value="2024"></el-option>
          <el-option disabled label="2025" value="2025"></el-option>
        </el-select>
        <div @keyup.enter="GetUsernameFocus()">
          <el-input
            v-model="major"
            placeholder=""
            class="login-input"
            v-if="showTextSelect"
            prefix-icon="el-icon-help"
            clearable
          />
        </div>
        <div @keyup.enter="GetPasswordFocus()">
          <el-input
            v-model="username"
            class="login-input"
            placeholder=""
            clearable
            maxlength="6"
            show-word-limit
            prefix-icon="el-icon-s-check"
            ref="signUsername"
          />
        </div>
        <div @keyup.enter="signClick()">
          <el-input
            v-model="password"
            class="login-input"
            placeholder=""
            type="password"
            prefix-icon="el-icon-lock"
            ref="signPasswrod"
            maxlength="10"
            clearable
            show-password
          />
        </div>

        <div class="agreement">
          <el-checkbox
            v-model="checkboxGPL"
            label="Comply with the GPL"
            class="agree-checkbox"
          ></el-checkbox>
        </div>
        <div class="button">
          <el-button
            @click="signClick()"
            :disabled="InDisable()"
            class="login-button"
            >{{ SignText }}</el-button
          >
        </div>
      </el-card>
    </div>
  </div>
</template>
  
  <script>
export default {
  data() {
    return {
      showTextSelect: false,
      college: "",
      year: "2023",
      username: "",
      password: "",
      major: "",
      checkboxGPL: true,
      SignText: "Sign up",
    };
  },
  mounted() {},
  methods: {
    //
    Tologin() {
      this.$router.replace("/");
    },
    //
    GetUsernameFocus() {
      this.$refs.signUsername.focus();
    },
    GetPasswordFocus() {
      this.$refs.signPasswrod.focus();
    },
    //
    signClick() {
      if (
        this.SignText == "Be registering" ||
        this.SignText == "Sign Successfully"
      )
        return;
      let bool = this.InDisable();
      if (bool) {
        this.$notify.warning({ message: "", title: "" });
        return;
      }
      this.SignText = "Be registering";
      const reg = /[\u4e00-\u9fa5]/g;
      if (!reg.test(this.username) || !reg.test(this.major)) {
        this.$message.warning({ message: "" });
        this.SignText = "Sign up";
        return;
      }
      this.$axios
        .post("/User/Sign", {
          username: this.username,
          password: this.password,
          college: this.college,
          major: this.major,
          year: this.year,
        })
        .then(
          (res) => {
            if (res.data.code == "1") {
              this.SignText = "Sign Successfully";
              localStorage.setItem("id", res.data.data);
              localStorage.setItem("token", res.data.token);
              this.$message.success({
                message: "",
                iconClass: "el-icon-s-promotion",
                center: true,
                duration: 500,
              });
              localStorage.setItem("LoginOne", "LoginOne");
              setTimeout(() => {
                this.$router.replace("/AssociationIndex");
                this.$bus.$emit("openWebSocket")
              }, 500);
            } else {
              this.SignText = "Sign up";
              this.$message.info({ message: "" });
            }
          },
          (err) => {
            this.$message.error({ message: "..." });
            console.log(err);
          }
        );
    },
    //
    changeMajorIt() {
      if (this.major == "") {
        this.$notify.info({ message: "", title: "" });
      }
    },
    //
    InDisable() {
      if (this.showTextSelect == false) {
        if (
          this.password == "" ||
          this.username == "" ||
          this.checkboxGPL == false
        ) {
          return true;
        }
      } else {
        if (
          this.username == "" ||
          this.password == "" ||
          this.major == "" ||
          this.checkboxGPL == false
        ) {
          return true;
        }
      }
    },
    //
    SelectCollege() {
      if (this.college != "") {
        this.major = "";
        this.showTextSelect = true;
      } else {
        this.major = "";
        this.showTextSelect = false;
      }
    },
  },
};
</script>
  
<style lang="less" scoped>
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f2f2f2;
  position: fixed;
  left: 50%;
  top: 50%;
  translate: -50% -50%;
  z-index: 999;
}

.login-card {
  width: 400px;
  text-align: center;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: white;
}

.logo {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.login-select {
  width: 100%;
  margin-bottom: 15px;
}

.login-input {
  width: 100%;
  margin-bottom: 15px;
  border-radius: 4px;
}

.agreement {
  display: flex;
  justify-content: center;
}

.agree-checkbox {
  margin-right: 5px;
}

.login-button {
  width: 100%;
  margin-top: 10px;
}
</style>